import React from 'react'
import GithubIcon from '../../icons/github.svg'
import LinkedinIcon from '../../icons/linkedin.svg'
import DevIcon from '../../icons/dev.svg'
import { useLocation } from '@reach/router'
import { Link } from 'gatsby'

const routes = [
  {
    path: '/',
    route: 'home',
  },
  {
    path: '/work',
    route: 'work',
  },
  {
    path: '/skillset',
    route: 'skillset',
  },
  {
    path: '/about',
    route: 'about',
  },
]

const Navbar = () => {
  const location = useLocation()

  function renderClassNameForLink(path) {
    const className = 'm-2 md:text-2xl font-bold border-b-4 border-transparent'
    if (location.pathname === path) {
      return className + ' text-yellow-400'
    } else {
      return (
        className +
        ' text-gray-200 hover:text-yellow-400 hover:border-yellow-400'
      )
    }
  }
  return (
    <nav className="px-3 w-32 md:fixed md:right-0 md:h-full">
      <section className="flex md:flex-col justify-start items-center md:h-1/2">
        {routes.map((route) => (
          <Link
            key={route.path}
            to={route.path}
            className={renderClassNameForLink(route.path)}
          >
            {route.route}
          </Link>
        ))}
      </section>

      <section className="flex md:flex-col justify-start items-center md:h-1/2">
        <a
          className="m-2"
          href="https://www.linkedin.com/in/khalil-abdelhamid/"
          target="_blank"
          rel="noreferrer"
        >
          <LinkedinIcon className="w-8 h-8 hover:opacity-50" />
        </a>
        <a
          href="https://github.com/ablil"
          className="m-2"
          target="_blank"
          rel="noreferrer"
        >
          <GithubIcon className="w-8 h-8 hover:opacity-50" />
        </a>
        <a
          href="https://dev.to/ablil"
          className="m-2"
          target="_blank"
          rel="noreferrer"
        >
          <DevIcon className="w-8 h-8 hover:opacity-50" />
        </a>
      </section>
    </nav>
  )
}

export default Navbar
